.popup__container {
	background: #FFFFFF;
	color: #000000;
	width: 430px;
	box-sizing: border-box;
	box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
	border-radius: 10px;
	padding: 34px 36px 37px;
	display: flex;
	flex-direction: column;
	align-items: stretch;
	position: relative;
}
@media screen and (max-width: 767px) {
	.popup__container {
		width: 282px;
		padding: 25px 22px;
	}
}